import React, { Fragment } from 'react'
import styled from 'styled-components'
import axios from 'axios'
import Iframe from 'react-iframe'
import Head from 'next/head'
import Main from '../layouts/main'

const ExperienceWrapper = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 50px auto;
    padding: 16px;
    
    h2 {
        font-size: 2.5rem;
    }
`

const Experience = styled.div`
    margin: 35px auto;
    width: 70%;
    display: flex;
    justify-content: center;
    border: 1px solid #ccc;
    border-radius: 10px;
    padding: 20px;
    -webkit-box-shadow: 0px 3px 5px 0px rgba(184,184,184,1);
    -moz-box-shadow: 0px 3px 5px 0px rgba(184,184,184,1);
    box-shadow: 0px 3px 5px 0px rgba(184,184,184,1);
    transition: 0.5s background;
    
    div:first-child {
        flex: 2;
        height: 100%;
    }
    
    div:last-child {
        flex: 1;
        margin-left: 15px;
        
        h1 {
            margin-bottom: 16px;
            text-align: center;
            font-size: 1.5rem;
        }
        
        p {
            text-align: justify;
        }
    }
    
    @media (max-width: 780px) {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
`

const CommentsWrapper = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 25px;
`

const Comment = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    border: 1px solid #ccc;
    border-radius: 10px;
    padding: 20px;
    -webkit-box-shadow: 0px 3px 5px 0px rgba(184,184,184,1);
    -moz-box-shadow: 0px 3px 5px 0px rgba(184,184,184,1);
    box-shadow: 0px 3px 5px 0px rgba(184,184,184,1);
    transition: 0.5s background;
    
    div {
        
        h3 {
            font-size: 1.3rem;
        }
        
        h4 {
            color: #ccc;
        }
    }
`

const Experiencia = props => {
    return (
        <Main>
            <Head>
                <title>OrtoConecta | {props.experience.title}</title>
            </Head>
            <ExperienceWrapper>
                <Experience>
                    <Fragment>
                        <div>
                            <Iframe url={`http://www.youtube.com/embed/${props.experience.video_url}`}
                                position="relative"
                                width="100%"
                                height="500px"
                                display="initial"
                                className="iframe"
                                allowFullScreen />
                        </div>
                        <div>
                            <h1>{props.experience.title}</h1>
                            <p>{props.experience.description}</p>
                        </div>
                    </Fragment>
                </Experience>
                <h2>Coment√°rios</h2>
                <CommentsWrapper>
                    <Comment>
                        {props.experience.comments.map(comment => (
                            <div key={comment.id}>
                                <h3>{comment.author}</h3>
                                <h4>{comment.author_email}</h4>
                            </div>
                        ))}
                    </Comment>
                </CommentsWrapper>
            </ExperienceWrapper>
        </Main>
    )
}

Experiencia.getInitialProps = async ({ query }) => {
    const experienceResponse = await axios.get(`http://ortoconecta-plataforma-brunogcpinheiro.c9users.io:8080/experiences/${query.id}`)
    
    return { experience: experienceResponse.data }
}

export default Experiencia


